	module.exports = {"pathContext":{"docHtml":"<h1>unpipe</h1>\n<p><a href=\"https://npmjs.org/package/unpipe\"><img src=\"https://img.shields.io/npm/v/unpipe.svg\" alt=\"NPM Version\"></a>\n<a href=\"https://npmjs.org/package/unpipe\"><img src=\"https://img.shields.io/npm/dm/unpipe.svg\" alt=\"NPM Downloads\"></a>\n<a href=\"http://nodejs.org/download/\"><img src=\"https://img.shields.io/node/v/unpipe.svg\" alt=\"Node.js Version\"></a>\n<a href=\"https://travis-ci.org/stream-utils/unpipe\"><img src=\"https://img.shields.io/travis/stream-utils/unpipe.svg\" alt=\"Build Status\"></a>\n<a href=\"https://coveralls.io/r/stream-utils/unpipe?branch=master\"><img src=\"https://img.shields.io/coveralls/stream-utils/unpipe.svg\" alt=\"Test Coverage\"></a></p>\n<p>Unpipe a stream from all destinations.</p>\n<h2>Installation</h2>\n<pre><code class=\"language-sh\">$ npm install unpipe\n</code></pre>\n<h2>API</h2>\n<pre><code class=\"language-js\">var unpipe = require('unpipe')\n</code></pre>\n<h3>unpipe(stream)</h3>\n<p>Unpipes all destinations from a given stream. With stream 2+, this is\nequivalent to <code>stream.unpipe()</code>. When used with streams 1 style streams\n(typically Node.js 0.8 and below), this module attempts to undo the\nactions done in <code>stream.pipe(dest)</code>.</p>\n<h2>License</h2>\n<p><a href=\"LICENSE\">MIT</a></p>","docPath":"argo-ci/node_modules/unpipe/readme","proj":"argo-ci"}}

